<template>
  <v-container class="visualcards">
    <v-row>
      <v-col class = "justify-center" cols="12" xs="12" sm="6" md="4" lg="4" xl="4" v-for="visual in visuals" :key="visual.cname">
        <div :class="'flip-card-container '+visual.name">
          <div class="card-inner" :id="'card-inner-'+visual.name" style="min-width: 260px">
            <v-card class="card-front d-flex flex-column rounded-xl">
              <v-card-title  class="justify-center card-title-text">{{ visual.cname }}</v-card-title>
              <!-- <v-card-title  :class="mark + ' justify-center card-title-text'">{{ visual.cname }}</v-card-title> -->
              <v-img
                height="150"
                max-height="150"
                contain
                :src='"../assets/image/"+ visual.imagePosition + ".png"'
              ></v-img>
              <v-divider class="mx-2 mt-3"></v-divider>
              <v-card-text style="height:200px">
                <div  height="20" class="subtitle" style="font-weight:bold">{{visual.subtitle}}</div>
                <div class="card-exp-text  mt-2">
                  {{visual.explanation}}
                </div>
              </v-card-text>
              <v-card-actions style="height: 68px" class="justify-center">
                <v-btn class="card-btn rounded-lg" text @click="btnClick(visual.name,'front')">
                  View the case
                </v-btn>
              </v-card-actions>
            </v-card>
            <v-card class="card-back d-flex flex-column rounded-xl">
              <v-card-title  class="justify-center card-title-text">{{ visual.cname}}</v-card-title> 
              <!-- <v-card-title  :class="mark + ' justify-center card-title-text'">{{ visual.cname}}</v-card-title> -->
               <!--  <v-card-title :class="episode + ' justify-center card-title-text'">{{visual.cname}}</v-card-title> -->
              <v-img
                height="180"
                max-height="180"
                contain
                :src='"../assets/gif/"+ visual.gifPosition + ".gif"'
              ></v-img>
              <v-divider class="mx-2 mt-3"></v-divider>
              <v-card-text style="height:200px">
                <div class="subtitle" style="font-weight:bold">Story case</div>
                <div class="card-exp-text mt-2">
                  {{visual.case}}
                </div>
                <div class="card-info-text mt-3">
                  Source: {{visual.author}}
                </div>
                <div class="card-info-text">
                  <a style="text-decoration: none;" :href=visual.link class="card-info-text"><v-icon x-small>mdi-link-variant</v-icon>URL</a>
                </div>
              </v-card-text>
              <v-card-actions style="height:68px" class="justify-center">
                <v-btn class="card-btn rounded-lg" text @click="btnClick(visual.name,'back')">
                  Go back to front
                </v-btn>
              </v-card-actions>
            </v-card>
          </div>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    visuals: Array,
    mark:String
  },
  methods:{
    btnClick: function(btnName,cardDirection){
      if(cardDirection == "front"){
        document.getElementById('card-inner-'+btnName).style.transform="rotateY(180deg)"
      }
      else{
        document.getElementById('card-inner-'+btnName).style.transform="rotateY(0deg)"
      }
      
    }
  }
};
</script>
<style>
/* .bottom-align{
  position: absolute;
  bottom: 0;
} */
.subtitle{
  color: #FF9C0D;
  font-size:1.2rem !important;
}
.card-btn{
  /* background-color:rgba(243, 223, 125,0.3); */
  background: linear-gradient(90deg, rgba(255,178,41,0.4) 0%, rgba(255,149,117,0.4) 100%);
}
.card-inner {
  position: relative;
  transform-style: preserve-3d;
  transition: 1s transform;
  max-width: 350px;
}
.flip-card-container{
  perspective: 40rem;
}
/* .card-inner:hover{
  transform: rotateY(180deg);
} */
.card-front, .card-back {
  backface-visibility: hidden;
}
.card-back {
  position: absolute !important;
  top: 0; right: 0; bottom: 0; left: 0;
  transform: rotateY(180deg);
}
.card-title-text{
  font-size:1rem;
}
.card-exp-text{
  font-size:0.8rem !important;
  line-height: 1rem !important;
  color:rgba(91,94,107);
}
.card-info-text{
  font-size:0.5rem !important;
  line-height: 1rem !important;
  color:grey !important;
}
/* .visualCardBtn{
  font-size:0.5rem !important;
  width: 97%;
  text-transform: none !important;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;}*/
</style>