<template>
  <div class="home">
    <v-navigation-drawer app clipped bottom class="navigation">
      <v-list>
        <v-list-group
          active-class="menu-title"
          v-for="episode in episodes"
          :key="episode.name"
          :id="episode.name"
        >
                  <!-- @click="checkEpisode(episode.name, episode.narrpatterns)" -->
          <v-list-item slot="activator">
            <v-list-item-content>
              <v-list-item-title>{{ episode.cname }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item
            v-for="narrpattern in episode.narrpatterns"
            :key="narrpattern.name"
            :id="'list-item-' + narrpattern.name"
            class="ml-8 pl-2 inactiveItemList"
          >
            <!--             @click="$vuetify.goTo('#'+narrpattern.name, options)" -->
            <v-list-item-content>
              <v-list-item-title>{{ narrpattern.cname }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
    <Episode
      :narrpatterns="data"
    >
    </Episode>
  </div>
</template>

<script>
// import Setting from '../components/Setting.vue'
// import Rising from '../components/Rising.vue'
import data from "../assets/data.json";
import Episode from "../components/Episode.vue";
import goTo from 'vuetify/es5/services/goto'

export default {
  name: "Home",
  components: {
    Episode,
  },
  data() {
    return {
      episodes: [
        {
          name: "setting",
          narrpatterns: data,
          cname: "Four Punctuation Marks",
        },
      ],
      // episode: "setting",
      data: data,
      narrpatterns:data,
      currSection: "Full-Stop-1",
    };
  },
  computed: {
    options() {
      return {
        duration: 300,
        offset: 64,
        easing: "easeInOutCubic",
      };
    },
  },
  methods:{
    intersectHeight(secID){
        var secEl = document.getElementById(secID)
        var xBottom = secEl.getBoundingClientRect().bottom
        var xTop = secEl.getBoundingClientRect().top
        var xHeight = secEl.getBoundingClientRect().height
        var viewHeight = window.innerHeight
        var currLength = 0
        // if intersected
        if (xTop < viewHeight && xBottom > 0){
          // case one, section across the viewport
          if (xBottom >= viewHeight && xTop <=0){
            currLength = viewHeight
          }
          // case two, section inside the viewport
          if (xBottom <= viewHeight && xTop >=0){
            currLength = xHeight
          }
          // case three upper section outside the viewport
          if (xBottom < viewHeight && xTop <0){
            currLength = xBottom
          }
          // case four lower section outside the viewport
          if (xBottom > viewHeight && xTop < viewHeight && xTop >0){
            currLength = viewHeight - xTop
          }
      }
      return currLength
    },
    handleScroll(){
      var maxID = "Full-Stop-1"
      var maxLength = 0
      var currLength = 0
      this.narrpatterns.map((secObj)=>{
        currLength = this.intersectHeight(secObj.name)
        if(maxLength < currLength){
          maxLength = currLength
          maxID = secObj.name
        }
        //console.log("sec: " + secObj.name + " secLength: " + currLength + "maxLength: " + maxLength + "maxID: " + maxID)
      })
      this.narrpatterns.map((secObj)=>{
        var listItem = document.getElementById("list-item-" + secObj.name);
        if(maxID == secObj.name){
          listItem.classList.add("active-list");
          listItem.classList.remove("inactiveItemList");
        }
        else{
          listItem.classList.remove("active-list");
          listItem.classList.add("inactiveItemList");
        }
      })
      this.currSection = maxID
    },
    scrollToNew(){
      if (this.currSection.includes(this.episode)){
        goTo('#'+this.currSection, {duration:300, offset: 100, easing: 'easeInOutCubic',})
        window.scrollBy(0, 1)
      }
    },
  },
  mounted() {
    //when components are mounted, click on setting automatically
    const setting = document.getElementById("setting").firstElementChild;
    setting.click();
    window.scrollBy(0, 1);
  },
  created() {
    document.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    document.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
<style>
.menu-title{
  background-color: rgb(210,210,210);
}
.active-list{
  background:linear-gradient(90deg, rgba(255,178,41,0.4) 0%, rgba(255,149,117,0.4) 100%);
  border-left: 2.5px solid rgb(128,128,128);
  border-radius: 0 5px 5px 0;
}
.stop{
  background-color:#4081BC;
}
.exclam{
  background-color:#FF681D;

}
.question{
  background-color:#FFC91D;

}
.ellipse{
  background-color:#40916A;

}
/* .v-list-item--link:before { background-color: transparent !important;} */
.v-list-item {
  min-height: 1.2rem !important;
}
.v-list-item__title {
  font-size: 1rem !important;
}
.inactiveItemList {
  background-color: transparent;
  border-left: 2.5px solid grey;
}
.v-list-group__header {
  border-radius: 5px 5px 5px 5px;
}
.navigation {
  padding-left: 0.7rem;
  padding-right: 0.7rem;
  /* background-color:#FDF8F4 !important; */
  /* background-color:whitesmoke; */
}
.v-list-group--active
  > .v-list-group__header
  > .v-list-group__header__append-icon
  .v-icon {
  color: white !important;
}
</style>
